
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Emotion from 'vux/src/components/wechat-emotion/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapState} from "vuex";
export default {
name:"topic",
props:["id"],
components:{
Group,
CellBox,
Flexbox,
FlexboxItem,
Emotion,
XTextarea,
XButton
},
computed:{
...mapState(['isLogined'])
},
data(){
return{
topic:null,
list:[''],
replyContent:""
}
},
methods:{

addReply:function(replyTab){


this.$http.post("/topic/"+this.topic.id+"/replies",{
accesstoken:this.$store.state.accesstoken,
content:this.replyContent
}).then(function(res){

})
},

collect:function(){
var that=this;
this.$http.post("/topic_collect/collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function(res){
console.log(res.data);

if(res.data && res.data.success){

that.topic.is_collect = true;
}

})
},

decollect:function(){
var that=this;
this.$http.post("/topic_collect/de_collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topic.id
}).then(function(res){
console.log(res.data);

if(res.data && res.data.success){

that.topic.is_collect = false;
}
})
}

},
/**/
beforeRouteEnter(to,from,next){
next(function (vm){
vm.$http.get("/topic/"+to.params.id,{

params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function(res){

vm.topic = res.data.data;
})
})
},
beforeRouteUpdate(to,from,next){
var that = this;
this.$http.get("/topic/"+to.params.id,{

params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){

that.topic = res.data.data;
})
}
}



