
import Msg from 'vux/src/components/msg/index.vue'

export default{
name:"loginout",
components:{
Msg
},
data(){
return{
isShowMsg:false,
buttons:[
{
text:"",
type:"primary",
link:"/login"
}
]
}
},
created(){

if(false==this.$store.state.isLogined){
this.isShowMsg=true;
return;
}

window.localStorage.clear();

this.$store.commit("mutationLongout");

this.$router.push("/topiclist/all")
}
}



