
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default{
name:"user",
props:["loginname"],
components:{
Card,
Cell,
Group,
},
data(){
return{
userInfo:null,
collect_topics:null
}
},
beforeRouteEnter(to,from,next){
next(function(vm){
vm.$http.get("/user/"+to.params.loginname).then(function(res){
console.log(res.data);
vm.userInfo = res.data.data;
});

vm.$http.get("/topic_collect/"+to.params.loginname).then(function(res){
console.log(res.data)
vm.collect_topics=res.data.data
})
})
},
beforeRouteUpdate(to,from,next){
this.$http.get("/user/"+to.params.loginname).then(function(res){
console.log(res.data);
that.userInfo = res.data.data;
});

this.$http.get("/topic_collect/"+to.params.loginname).then(function(res){
console.log(res.data)
vm.collect_topics=res.data.data
})
next();
}
}



