
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import {mapState, mapGetters } from "vuex";
export default {
name:"topiclist",
props:["name"],
components:{
Group,
Cell,
Scroller,
Tab,
TabItem
},
data(){
return{
lists:[],
page:1, //
limit:15, //

pullupConfig: {
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
},

pulldownConfig:{
content: '',
downContent: '',
upContent: '',
loadingContent: '...'
}
};
},
computed:{
...mapGetters(["getTopicDescByTab"]),
...mapState(["topics"])
},

methods:{

routeto: function(index) {

console.log(index);






this.$router.push('/topiclist/' + this.topics[index].name);
},
loadMoreData: function() {//,ajax
var that =this;
this.page = this.page+1;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
for (let i = 0; i<response.data.data.length; i++) {
that.lists.push(response.data.data[i]);
}

that.$refs.pullup.donePullup();
}

})
},

freshData:function(){
var that = this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){
if(res.data && res.data.success){
that.lists = res.data.data;
that.page = 1;
that.$refs.pullup.donePulldown();
}
})
}
},


beforeRouteEnter(to,from,next){






next(function(vm){

vm.$http.get("/topics",{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response) {

if(response.data.success){
vm.lists = response.data.data;
}
})
});
},

beforeRouteUpdate:function(to,from,next){
var that = this;
this.page = 1;     

this.$http.get("/topics",{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
if(response.data.success){
that.lists = response.data.data;
}
});

next();
},

}



