
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

import { mavonEditor } from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
export default {
name: "create",
components: {
XTextarea,
Group,
XButton,
Selector,
mavonEditor
},
data() {
return {
title: "",
content: "",
value: "",
list: ["", "", "", "", ""],
toolbars: {
bold: true, // 
italic: true, // 
header: true, // 
underline: true, // 
quote: true, // 
ol: true, // 
ul: true, // 
link: true, // 
imagelink: true, // 
code: true, // code
table: true, // 
fullscreen: true, // 
htmlcode: true, // html
help: true, // 
/* 2.1.8 */
alignleft: true, // 
aligncenter: true, // 
alignright: true, // 
/* 2.2.1 */
subfield: true, // 
preview: true, // 
}
}
},
methods: {

uploadImg:function  (pos,$file) {
var that = this;

console.log(pos);

console.log($file);
var params = new FormData();
params.append("smfile",file)

axios.post("https://sm.ms/api/v2/upload",params,{



headers:{"Content-type":"multipart/form-data"}
}).then(function  (res) {

this.$refs.md.$img2Url(pos,res.data.data.url)
})

},
addContent: function() {
this.$http.post("topics", {
title: this.title,
content: this.content,
tab: "dev",
accesstoken: this.$store.state.accesstoken
}).then(function() {
if(res.data.seccess == true) {
that.$router.replace("/topic/" + res.data.topic_id);
}
})
}
}
}



